import React, { Component } from 'react';
import { Link } from 'react-router-dom';

export default class BottomNavBar extends Component{
    render() {
        return (
          <div className='w3-center  ' style={{maxWidth:"500px"}}>
                  {/*bottom nav bar*/}
                <div className=" w3-light-grey w3-round-xlarge w3-margin-top w3-margin-left w3-block  " style={{minHeight:"70px", maxWidth:"93%",}}>
                    <div className="w3-center  ">
                        <Link to="/">
                            
                             <i className=" bx bx-user icon w3-button w3-round-xxlarge  w3-xlarge w3-margin-top w3-hover-dark-blue" style={{}} ></i>
                        </Link>
                        <Link to="/search">
                             <i className=" bx bx-search icon w3-button w3-round-xxlarge w3-xlarge w3-margin-top w3-hover-dark-blue" style={{margin:"0px 60px"}}></i>
                        </Link>
                        <Link to="/settings">
                             <i className="  bx bx-cog icon w3-button w3-round-xxlarge w3-xlarge w3-margin-top w3-hover-dark-blue"></i>
                        </Link>
                    </div>
    
                </div >
             </div>
        );
    }
}