import React, { Component } from 'react';
import { Link } from 'react-router-dom';

export default class AccountWidget extends Component{

    render() {
        return (
            <div className='w3-container w3-margin-top w3-margin-left' style={{marginTop:"-30px", maxWidth:"500px", marginBottom:"40px"}}>
                <Link to="/settings">
                    <div className=" ">

                        <i className="bx bx-x icon w3-button w3-round-xxlarge w3-xlarge w3-margin-top w3-hover-dark-blue" style={{marginLeft:"-25px"}} ></i>
                        <i className="bx bx-check icon w3-button w3-round-xxlarge w3-dark-blue w3-xlarge w3-margin-top w3-text-white  w3-right" ></i>

                    </div>
                </Link>

                <div>
                    <p className='w3-xxlarge' style={{marginTop:"30px", fontWeight:"bold"}}>Account</p>

                </div>

                <div >
                    <p className='w3-text-grey'>Photo</p>
                    <div style={{marginLeft:"100px", fontSize:"15px"}}>

                        <i  className="bx bxs-user icon bx-border-circle w3-jumbo w3-light-grey w3-text-grey " ></i>
                        <p className='w3-text-grey'>Upload Image</p>
                    </div>
                </div>

                <div style={{marginTop:"60px"}}>
                    <span className='w3-text-grey'>Name</span>
                    <span style={{marginLeft:"68px", fontSize:"15px"}}>Zamir Mubarak</span>
                </div>

                <div style={{marginTop:"40px"}}>
                    <span className='w3-text-grey'>Gender</span>
                    <span style={{marginLeft:"55px", fontSize:"15px"}}>

                         <i className="bx bx-male-sign icon bx-border-circle w3-dark-blue w3-text-white bx-sm" ></i>
                         <i className="bx bx-female-sign icon  bx-border-circle  w3-text-grey w3-light-grey bx-sm w3-margin-left" ></i>
                    </span>
             </div>

              
                <div style={{marginTop:"40px"}}> 
                    <span className='w3-text-grey'>Age</span>
                    <span style={{marginLeft:"80px", fontSize:"15px"}}>23</span>
                </div>
              
                <div style={{marginTop:"40px"}}>
                    <span className='w3-text-grey'>Email</span>
                    <span style={{marginLeft:"74px", fontSize:"15px"}}>mubaraklawal52@gmail.com</span>
                </div>
                






            </div>
        );
    }
}