import React, { Component } from 'react';
import { Link } from 'react-router-dom';





export default class SearchWidget extends Component {



render() {
    return (
        <div className="w3-container pulp-display-font w3-margin-top" style={{maxWidth:"500px"}}>
            <Link to="/">
              <div className="w3-center">

                  <i className="bx bx-x icon w3-button w3-round-xxlarge w3-light-grey w3-xlarge w3-margin-top w3-hover-dark-blue" ></i>

              </div>

            </Link>

            {/* search widget */}
            <div className="w3-margin-top w3-row w3-light-grey w3-round-xlarge w3-margin-bottom">

                    <i style={{marginBottom:"18px",}} className="bx bx-search icon w3-button w3-round-xxlarge w3-light-grey w3-xlarge w3-margin-top w3-hover-dark-blue" ></i>

                    <input className="w3-light-grey" style={{outline:"none", border:"none", fontSize:"16px"}} placeholder="Enter text here"/>
                
            </div>

            {/* hire rate tag buttons */}
            <div className=''>
                <div>
                    <span className="w3-button w3-round-xlarge w3-dark-blue w3-text-white w3-half" style={{minWidth:"120px",  maxWidth:"50%",  maxHeight:"70px", borderRadius:"24px"}}><p style={{fontSize:"15px"}}>Full time</p></span>
                    <span className="w3-button w3-round-xlarge w3-light-grey w3-half" style={{ minWidth:"120px", maxWidth:"45%",  maxHeight:"70px", marginLeft:"5%", borderRadius:"24px", marginBottom:"15px"}}><p style={{fontSize:"15px"}}>Part time</p></span>
                </div>
                
                <div>
                    <span className="w3-button w3-round-xlarge w3-light-grey w3-half   " style={{minWidth:"120px",   maxWidth:"50%",  maxHeight:"70px", borderRadius:"24px"}}><p style={{fontSize:"15px"}}>Fixed</p></span>
                    <span className=" w3-button w3-round-xlarge w3-dark-blue w3-text-white w3-half" style={{ minWidth:"120px", maxWidth:"45%",  maxHeight:"70px", marginLeft:"5%", borderRadius:"24px", marginBottom:"15px"}}><p style={{fontSize:"15px"}}>Hourly</p></span>
                </div>
            </div>
          
            {/* hire price range slider widget */}
           
              <div className='w3-center'>
                  <span style={{minWidth:"20%",marginTop:"35px"}} className='w3-light-grey w3-button w3-text-black w3-round-xlarge '>$<span className='w3-text-black pulp-display-font' id="value"></span></span>
              </div>

          
          

              <div style={{marginTop:"20px"}}>
                  <input  id="range" type="range" min="1" max="100"/>
                  <div className='slider-container'>
                    <svg id="slider"  viewBox='0 0 500 50' >
                        <circle  id="thumb" stroke="#021768" fill="#021768" r="12" />
                        <path id="curve" stroke="#021768" stroke-width="3" fill="none" stroke-dasharray="18, 15"/>
                    </svg>
                  </div>
              </div>

              <div className='w3-container' style={{marginTop:"-5px"}} >
                  <div  className=''>
                    <svg viewBox='-9 -9 400 30' >
                     <circle stroke="#f1f1f1" fill="#f1f1f1" r="8" />
                    </svg>
                  </div>

                  <div style={{marginTop:"-35px"}}>
                    <svg viewBox='-399 -9 410 30' >
                     <circle stroke="#f1f1f1" fill="#f1f1f1" r="8" />
                    </svg>
                  </div>
        
                  <span className=' w3-text-black w3-left'>$0</span>
                  <span  className=' w3-text-black w3-right '>$240+</span>

              </div>
                          

            

            
            {/* show result button */}
            <Link to="/result" style={{textDecoration:"none", marginBottom:"50px"}}>
              <div style={{marginTop:"90px", maxWidth:"90%", marginLeft:"20px"}} className='w3-center'>
                  <button className="w3-button w3-block w3-dark-blue w3-text-white w3-round-xlarge "><p style={{fontSize:"15px",}}>Show 75 results</p></button>
              </div>
            </Link>


      
            <br/>
            <br/>
       


            

       

            

            


        </div>


);
}


componentDidMount(){
    const curve = {
        x: 50, //moves start x to right
        y: 50,
        cpx: 250,
        cpy: -25, //moves the height of the curve
        endx: 450,
        endy:50 ,
      };
      
      let percent = 0.50;
      
      let curveEl = document.getElementById("curve");
      let thumbEl = document.getElementById("thumb");
      
      // get the XY at the specified percentage along the curve
      const getQuadraticBezierXYatPercent = (curve, percent) => {
        let x =
          Math.pow(1 - percent, 2) * curve.x +
          2 * (1 - percent) * percent * curve.cpx +
          Math.pow(percent, 2) * curve.endx;
        let y =
          Math.pow(1 - percent, 2) * curve.y +
          2 * (1 - percent) * percent * curve.cpy +
          Math.pow(percent, 2) * curve.endy;
      
        return { x, y };
      };
      
      const drawCurve = () => {
        curveEl.setAttribute(
          "d",
          `M${curve.x},${curve.y} Q${curve.cpx},${curve.cpy} ${curve.endx},${curve.endy}`
        );
      };
      
      const drawThumb = (percent) => {
        let pos = getQuadraticBezierXYatPercent(curve, percent);
        document.getElementById("value").textContent = percent * 240; //240 is a multiplier value 
      
        thumbEl.setAttribute("cx", pos.x);
        thumbEl.setAttribute("cy", pos.y);
      };
      
      const moveThumb = (e) => {
        console.log(e.target.value);
        percent = e.target.value / 100;
        drawThumb(percent);
      };
      
      // event on the range input
      var rangeEl = document.getElementById("range");
      rangeEl.value = percent * 100;
      rangeEl.addEventListener("input", moveThumb);
      
      // init
      drawCurve();
      drawThumb(percent);
      
}

}





