import React, { Component } from 'react';
import { Routes ,Route } from 'react-router-dom';
import {BrowserRouter} from "react-router-dom"

import ProfileWidget from './profile.jsx';
import SearchResultWidget from './searchresult.jsx';
import SearchWidget from './search.jsx';
import Error404Widget from './404page.jsx';
import SignUpWidget from './signup.jsx';
import LoginWidget from './login.jsx';
import SettingsWidget from "./settings.jsx"
import AccountWidget from './account.jsx';


import "../css/w3.css"





/* i kuku get pulp display font  and json server nomatchpage*/

export default class tradeApp extends Component{

render(){
    return (
        <BrowserRouter>
        
            <Routes>
                <Route path='/' element={<ProfileWidget/>} />
                <Route path='/search' element={<SearchWidget/>} />
                <Route path='/result' element={<SearchResultWidget/>} />
                <Route path='/signup' element={<SignUpWidget/>} />
                <Route path='/login' element={<LoginWidget/>} />
                <Route path='/settings' element={<SettingsWidget/>} />
                <Route path='/account' element={<AccountWidget/>} />
                

                <Route path='*' element={<Error404Widget/>} />
                



            </Routes>
        
          
        </BrowserRouter>
    )
}

}


