import React, { Component } from 'react';
import BottomNavBar from './bottomnavbar';
import ShowCaseWidget from './showcase';
import { Link } from 'react-router-dom';

import jacob from "../media/jacob.png"
import image1 from "../media/image1.jpg"
import image2 from "../media/image2.jpg"
import image3 from "../media/image3.jpg"


export default class ProfileWidget extends Component{
    render(){
        return (
            <div>
                
                
                <div className='w3-center w3-container w3-half' style={{maxWidth:"500px"}}>

                    <div>
                        <p >you can signup <Link to="/signup">here</Link></p>
                    </div>


                    <div className= "pulp-display-font "  >

                        {/* user image small this image gets hidden on large devices */}
                        <div className="w3-margin-top">     
                            <img className=" w3-hide-large w3-hide-medium"  style={{borderRadius:"30px"}} src={jacob} alt='User' width='90%'/>
                        </div>

                        {/* user image large this image gets hidden on small devices */}
                        <div>     
                            <img className="w3-hide-small" style={{borderRadius:"30px"}} src={jacob} alt='User' width='90%'/>
                        </div>

                        {/* user name and description */}
                        <div className="w3-container w3-margin-bottom"  >
                        <p className="w3-xlarge  w3-text-black" style={{textAlign:"justify", fontWeight: 'bold', }} >Jacob Roberts</p>
                        <p className="w3-text-grey " style={{textAlign:"justify",}} >Photographer. Work experience 7 years. I make nature and product photography </p>

                        </div>

                        {/* user works and experience , images of past works */}
                        <div className="w3-container " >
                                <div className=" w3-button  w3-light-grey w3-block  " style={{minHeight:"70px", maxWidth:"99%", borderRadius:"24px"}} >
                                    
                                    <div style={{marginTop:"8px"}}>
                                        <b >
                                            <span className="w3-xlarge" style={{ marginRight:"50px", fontWeight:'bold', }} >112 
                                                <span className="w3-small" style={{ fontWeight:'bold' , marginLeft:"5px"}}>  works</span>
                                                
                                                <span style={{marginLeft:"58%", }}>
                                                    <img className="w3-round-xlarge overlap-image1" src={image1} alt='Profile' width='15%'/>
                                                    <img className="w3-round-xlarge overlap-image2" src={image2} alt='Profile' width='15%'/>
                                                    <img className="w3-round-xlarge overlap-image3" src={image3} alt='Profile' width='15%'/>
                                                </span>

                                            </span> 
                                        </b>
                                    </div>

                                </div>

                                {/* user views*/}
                                <div className="w3-margin-top">
                                    <div className="">

                                        <span className=" w3-button w3-text-white w3-half " style={{backgroundColor:"#021768", maxWidth:"50%",  maxHeight:"70px", borderRadius:"24px"}} >    
                                            <p className="w3-xlarge" style={{ fontWeight: 'bold', marginBottom:"-10px", marginTop:"-2px", paddingRight:"75px"}}>3</p>
                                            <p className="w3-tiny" style={{marginBottom:"20px", paddingRight:"35px"}} >applications</p>
                                        </span>
                                        
                                        <span className=" w3-button w3-light-grey w3-half" style={{ minWidth:"120px", maxWidth:"45%",  maxHeight:"70px", marginLeft:"5%", borderRadius:"24px"}} >    
                                            <p className="w3-xlarge" style={{fontWeight: 'bold', marginBottom:"-10px", marginTop:"-2px", paddingRight:"60px"}}>25</p>
                                            <p className="w3-tiny w3-text-grey" style={{ marginBottom:"20px", paddingRight:"35px"}} >views today</p>
                                        </span>
                                    </div>

                                </div>
                    
                        





                        </div>

                    </div>

                    <BottomNavBar/>

                    <br/>
                    

                </div>

                   

                <div className='w3-half w3-right'>

                    

                   <ShowCaseWidget/>
                </div>
            </div>
        )
    }
}
