import React, { Component } from 'react';
import { Link } from 'react-router-dom';

export default class SettingsWidget extends Component{

    render() {
        return (
            <div className='w3-container w3-margin-top w3-margin-left' style={{ maxWidth:"500px", marginBottom:"40px"}}>
                <Link to="/#">
                    <div >

                        <i className="bx bx-chevron-left icon w3-button w3-round-xxlarge w3-xlarge w3-margin-top " style={{marginLeft:"-20px"}}  ></i>

                    </div>
                </Link>

                <div>
                    <p className='w3-xxlarge' style={{marginTop:"30px"}}>Settings</p>
                    <p className='w3-xlarge' style={{marginTop:"30px"}}>Account</p>

                </div>

                <div>
                    <span>

                        <i  className="  bx bx-user icon w3-button w3-round-xxlarge w3-light-grey w3-xlarge"  ></i>
                    </span>
                    <span className=' w3-large  w3-margin-left' > Zamir Mubarak </span>
                    
                    <span>
                        <Link to="/account">
                            <i className="bx bx-chevron-right icon w3-button w3-round-xxlarge w3-light-grey w3-xlarge  w3-right" ></i>
                        </Link>

                    </span>
                    
                </div>
                <br/><br/>

                <p className='w3-xlarge' style={{marginTop:"30px"}}>Settings</p>

                
                <div style={{marginTop:"30px"}} >
                    <i className="bx bx-world icon w3-button w3-round-xxlarge w3-light-grey w3-xlarge  w3-hover-dark-blue w3-left" ></i>
                    <span className='w3-large w3-margin-left'> Language </span>
                    <span className=' w3-margin-left w3-text-grey '> English </span>
                    <i className="bx bx-chevron-right icon w3-button w3-round-xxlarge w3-light-grey w3-xlarge  w3-hover-dark-blue w3-right" ></i>
                </div>
                <br/>

                <div style={{marginTop:"30px"}} >
                    <i className="bx bxs-bell icon w3-button w3-round-xxlarge w3-light-grey w3-xlarge  w3-hover-dark-blue w3-left" ></i>
                    <span className='w3-large w3-margin-left '> Notifications </span>
                 
                    <i className="bx bx-chevron-right icon w3-button w3-round-xxlarge w3-light-grey w3-xlarge  w3-hover-dark-blue w3-right" ></i>
                </div>
                <br/>

                <div style={{marginTop:"30px"}} >
                    <i className="bx bxs-moon icon w3-button w3-round-xxlarge w3-light-grey w3-xlarge w3-hover-dark-blue w3-left" ></i>
                    <span className='w3-large w3-margin-left'> Dark Mode </span>
                    <span className=' w3-margin-left w3-text-grey'> Off </span>
                    <i className="bx bx-toggle-left icon w3-button w3-round-xxlarge w3-light-grey w3-xlarge  w3-hover-dark-blue w3-right" ></i>
                </div>
                <br/>

                <div style={{marginTop:"30px"}}  >
                    <i className="bx bx-support icon w3-button w3-round-xxlarge w3-light-grey w3-xlarge  w3-hover-dark-blue w3-left" ></i>
                    <span className='w3-large w3-margin-left'> Help </span>
                    <i className="bx bx-chevron-right icon w3-button w3-round-xxlarge w3-light-grey w3-xlarge  w3-hover-dark-blue w3-right" ></i>
                </div>


              
                






            </div>
        );
    }
}