import React, { Component } from 'react';
import { Link } from 'react-router-dom';

export default class SignUpWidget extends Component{
    render() {
        return (
             <div className='pulp-display-font ' style={{marginTop:"-30px", maxWidth:"500px"}} >
                
                <div className='signup-cover w3-margin-top ' style={{height:"400px"}}>
                </div>
                <div className='w3-container w3-text-white w3-round-xxlarge  ' style={{marginTop:"-100px", }} >
                    <h4 className='w3-xlarge' style={{ fontWeight:"bold" }}> Get Started</h4>
                

                    <div className="w3-margin-top w3-light-grey w3-round-xlarge w3-margin-bottom">
                        <i style={{marginBottom:"18px",}} className="w3-text-dark-blue bx bx-user icon w3-button w3-hover-none w3-round-xxlarge w3-light-grey w3-xlarge w3-margin-top w3-hover-dark-blue" ></i>
                        <input className="w3-light-grey w3-text-dark-blue " style={{outline:"none", border:"none", fontSize:"16px"}} placeholder="Name" />
                    </div>

                    <div className="w3-margin-top  w3-light-grey w3-round-xlarge w3-margin-bottom">
                        <i style={{marginBottom:"18px",}} className="w3-text-dark-blue bx bx-envelope icon w3-button w3-hover-none w3-round-xxlarge w3-light-grey w3-xlarge w3-margin-top w3-hover-dark-blue" ></i>
                        <input className="w3-light-grey w3-text-dark-blue" style={{outline:"none", border:"none", fontSize:"16px"}} placeholder="Email" type="email"/>
                    </div>

                    <div className="w3-margin-top  w3-light-grey w3-round-xlarge w3-margin-bottom">
                        <i style={{marginBottom:"18px",}} className="w3-text-dark-blue bx bx-key icon w3-button w3-hover-none w3-round-xxlarge w3-light-grey w3-xlarge w3-margin-top w3-hover-dark-blue" ></i>
                        <input className="w3-light-grey w3-text-dark-blue" style={{outline:"none", border:"none", fontSize:"16px"}} placeholder="Password" type="password"/>
                    </div>

                    <div className="w3-margin-top  w3-light-grey w3-round-xlarge w3-margin-bottom">
                        <i style={{marginBottom:"18px",}} className="w3-text-dark-blue bx bx-key icon w3-button w3-hover-none w3-round-xxlarge w3-light-grey w3-xlarge w3-margin-top w3-hover-dark-blue" ></i>
                        <input className="w3-light-grey w3-text-dark-blue" style={{outline:"none", border:"none", fontSize:"16px"}} placeholder="Password confirmation" type="password"/>
                    </div>
                    
                    <div className='w3-container w3-text-black'>
                        <input type="checkbox" required />
                        <span className='w3-margin-left'>I agree to the
                            <a className='w3-text-dark-blue' href='/#'> Terms of Service</a> and <a className='w3-text-dark-blue' href='/#'> Privacy Policy</a> 
                        </span>

                    </div>

                    <div className='w3-container w3-text-black w3-padding-32'>
                        <span>Already have an account ? Login </span>
                        <Link  to="/login" className='w3-text-dark-blue'>Here</Link>
                    </div>

                    <Link to="/#" style={{textDecoration:"none"}}>
                        <div style={{ marginBottom:"30px" }} className='w3-center'>
                            <button className="w3-button w3-block w3-dark-blue w3-text-white w3-round-xlarge "><p style={{fontSize:"15px",}}>Sign Up</p></button>
                        </div>
                    </Link>

                    





                </div>

                

             </div>
        );
    }





}