import React, { Component } from 'react';
import image1 from "../media/image1.jpg"
import image2 from "../media/image2.jpg"
import image3 from "../media/image3.jpg"
import image4 from "../media/image4.jpg"
import image5 from "../media/image5.jpg"
import image6 from "../media/image6.jpg"

export default class ShowCaseWidget extends Component{

    render() {
        return (
             <div className='w3-container w3-hide-small w3-hide-medium' style={{marginLeft:"-10%"}}>
                <img className="w3-round-xxlarge w3-margin  " src={image1} alt='Profile' width='33%'/>
                <img className="w3-round-xxlarge w3-margin  " src={image2} alt='Profile' width='33%'/>
                <img className="w3-round-xxlarge w3-margin  " src={image5} alt='Profile' width='33%'/>
                <img className="w3-round-xxlarge w3-margin  " src={image3} alt='Profile' width='33%'/>
                <img className="w3-round-xxlarge w3-margin  " src={image6} alt='Profile' width='33%'/>
                <img className="w3-round-xxlarge w3-margin  " src={image4} alt='Profile' width='33%'/>

             </div>
        );
    }
}