import React, { Component } from 'react';
import { Link } from 'react-router-dom';

export default class Error404Widget extends Component{

    render() {
        return (
             <div className='w3-center' style={{marginTop:"10%"}}>
                <i style={{marginBottom:"18px",}} className="bx bx-error icon w3-round-xxlarge w3-jumbo" ></i>



                 <p className='pulp-display-font w3-jumbo '>Oops, you found this page :)</p>
                     <p >you can go back <Link to="/#">here</Link></p>
             </div>
        );
    }
}