import React, { Component } from 'react';
import BottomNavBar from './bottomnavbar';
import { Link } from 'react-router-dom';

export default class SearchResultWidget extends Component{

    render() {
        return (

            <div className='w3-container' style={{maxWidth:"500px"}}>
             <div className="w3-container pulp-display-font w3-padding-16 ">
                 {/* navigation block */}
                <div className="w3-block">
                    <Link to="/search">
                        <i className="bx bx-chevron-left  icon w3-button w3-round-xxlarge w3-light-grey w3-xlarge w3-margin-top w3-hover-dark-blue w3-left" ></i>
                        
                    </Link>
                     <div className="">

                        
                        <div>                    
                         <i className="bx bxs-toggle-right w3-right  w3-button w3-round-xxlarge w3-light-grey w3-xlarge w3-margin-top w3-hover-dark-blue " ></i>

                         
                         </div>
                     </div>



                </div>

            
                

                {/* result text */}
                <br/>
                <div className="w3-xxlarge" style={{ textAlign:"justify", marginTop:"60px", maxWidth:"90%"}}>
                    <b style={{fontWeight:"bold"}}>74 results for 'photographer'</b>

                </div>

                {/* result text */}
                <div style={{marginTop:"0px", backgroundColor:"#021768", borderRadius:"40px", textAlign:""}} className="">
                    <div className="w3-margin-top" style={{margin:"0px 30px" , }}>
                        <br/>
                        <b className="w3-xlarge w3-margin-right w3-text-white">Photographer</b>
                        <i className="bx bx-bookmark-minus icon w3-button w3-round-xxlarge w3-text-white w3-xlarge w3-right " style={{backgroundColor:"#303f7c",}} ></i>
                        <br/>
                        <p className="w3-button w3-round-xlarge w3-white w3-text-dark-blue"><b>$120/h</b></p>
                        <p style={{maxWidth:"90%", textAlign:""}} className="w3-opacity w3-text-white">Subject and studio photography of goods for an online store.
                            Photo processing.
                        </p>
                        
                        <div style={{marginTop:"20px",}}>

                        <span className=" w3-button w3-round-xlarge w3-tiny w3-text-white " style={{backgroundColor:"#303f7c"}}>Photography</span>
                        <span className=" w3-button w3-round-xlarge w3-tiny w3-text-white w3-margin-left " style={{backgroundColor:"#303f7c"}}>Photoshop</span>

                        </div>
                        <br/>
                    
                    </div>


                </div>
                    {/* like and dislike arrow button */}
                    <div class="things">
                        <div class="content">
                            <div class="arrow">
                            <div class="curve"></div>
                            <div class="point"></div>
                            </div>
                        </div>

                        <div class="content">
                            <div class="arrow2">
                            <div class="curve2"></div>
                            <div class="point2"></div>
                            </div>
                        </div>
                    </div>

                    <div className='w3-container'>
                        <span className=' w3-text-black w3-left' style={{marginTop:"35px"}}>Dislike</span>
                        <span className=' w3-text-black w3-right' style={{marginTop:"35px"}}>Like</span>
                    </div>





                </div>

                <BottomNavBar />
                <br/>
            </div>
        );
    }
}